import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: { 
    //定义一个全局的状态
    LoadingConfig:{
      isShowLoading:true,
      loadingText:"正在加载"
    }
  },
  mutations: {
    showLoading(state,txt){//state是当前Vuex对象的state
      state.LoadingConfig.isShowLoading=true;
      state.LoadingConfig.loadingText=txt;
    },
    hideLoading(state){
      state.LoadingConfig.isShowLoading=false;
    }
  },
  actions: {
    aShowLoading({commit},txt){//第一个参数是解构出来的commit，第二个参数是组件里调用dispatch传入的
      commit("showLoading",txt);//提交一个请求给vuex，让vuex调用mutations里面的方法去改变
    },
    aHideLoading({commit}){
      commit("hideLoading");
    }
  },
  modules: {
  },
  getters:{//一般使用getters里的方法用于读取状态
    isShowLoading(state){
      return state.LoadingConfig.isShowLoading;
    },
    loadingText(state){
      return state.LoadingConfig.loadingText;
    }
  }
})
