<template>
  <div id="app">
    <!-- <router-view/> -->
    <loading v-show="$store.getters.isShowLoading"></loading>
  </div>
</template>

<script>
import {mapGetters,mapActions} from "vuex" //导入vuex提供的mapGetters与mapActions
export default {
  name: "APP",
  methods: {
    ...mapActions(["aShowLoading","aHideLoading"]),//需要Actions里的哪些方法就扩展哪些
  },
  computed: {
    ...mapGetters(["isShowLoading","loadingText"]),//需要getters里的哪些方法就扩展哪些
  },
  created() {
    this.aShowLoading("正在加载中");//经扩展的方法即可直接使用
    setTimeout(() => {
      this.aHideLoading();
      this.aShowLoading("正在登录中");
      setTimeout(() => {
        this.aHideLoading();
      }, 2000);
    }, 2000);
  }
};
</script>

